import { IUser } from '../interfaces';

interface IRegx {
  [key: string]: RegExp;
}
const regx: IRegx = {
  kr: /[-|-|-]/,
  en: /[A-Za-z]/,
  uq: /[0-9~!@#$%^&*()_+|<>?:{}]/,
};

/**
 *
 * @param data - github api  data
 * @description github data        
 */
function formatData(data: any[]) {
  return data.map((item) => ({
    id: item.id,
    profile_image: item.avatar_url,
    name: item.login,
    isStar: false,
  }));
}

/**
 *
 * @param data - formatData   github 
 * @param stars -    
 * @description github        isStar  
 */
function checkIsStar(data: IUser[], stars: IUser[]) {
  return data.map((item) => {
    const isStart = stars.find((start) => start.id === item.id);
    if (isStart) {
      item.isStar = true;
    }
    return item;
  });
}

/**
 *
 * @param data - formatData   github 
 * @description  -   
 */
function sortStr(data: IUser[]) {
  return data.sort((a: IUser, b: IUser) => a.name.localeCompare(b.name, 'ko-KR'));
}

/**
 *
 * @param array ,    
 * @returns  -    
 */
function sortStringNumber(array: any[]) {
  let str: string[] = [];
  let uniq: number[] = [];
  array.forEach((item) => {
    regx['uq'].test(item) ? uniq.push(item) : str.push(item);
  });
  return [...str, ...uniq];
}

/**
 *
 * @param data - formatData   github 
 * @description  & ..    
 */
function groypByFirst(data: IUser[]) {
  const group = sortStr(data).reduce((acc, user) => {
    let first;
    regx['kr'].test(user.name) ? (first = getKorean(user.name)) : (first = user.name.charAt(0).toUpperCase());
    if (!acc[first]) {
      acc[first] = [];
    }
    acc[first].push(user);
    return acc;
  }, <Record<string, IUser[]>>{});

  const keys = sortStringNumber(Object.keys(group));

  return { keys, group };
}

/**
 *
 * @param kor - korean name
 * @description     
 */
function getKorean(kor: string) {
  const f = [
    '',
    '', //
    '',
    '',
    '', // 
    '',
    '',
    '',
    '', // 
    '',
    '', // 
    '',
    '',
    '', // 
    '',
    '',
    '',
    '',
    '',
  ];
  const ga: number = 44032;
  let uni: number = kor.charCodeAt(0);

  uni = uni - ga;

  let fn = Math.floor(uni / 588);

  return f[fn];
}

export { formatData, checkIsStar, groypByFirst };
